        <CodeBlock>{'float fMargin = 10;  // \n\n// 6\nDog[] objs = new Dog[6];\n\nvoid setup()\n{\n  size( 400, 400 );\n  colorMode( HSB );\n\n  float fStartY = fMargin;\n  \n  // \n  float fIntervalY = ( height - 2 * fMargin ) / float( objs.length );\n\n  // Dog\n  for ( int iObj = 0; iObj < objs.length; iObj++ )\n  {\n    objs[iObj] = new Dog( fStartY + iObj * fIntervalY );\n  }\n}\nvoid draw()\n{\n  background( 204 );\n\n  for ( int iObj = 0; iObj < objs.length; iObj++ )\n  {\n    objs[iObj].update();  // \n    objs[iObj].draw();    // \n  }\n}\n\nclass Dog\n{\n  float m_fPosX;      // X\n  float m_fPosY;      // Y\n  float m_fSpeedX;    // X\n  float m_fSwing;     // (0~360)\n  float m_fHue;       // (Hue)\n  float m_fTailLength;// \n  float m_fEarDeg;    // \n\n  Dog ( float fPosY )\n  {\n    m_fPosX = random( width );\n    m_fPosY = fPosY;\n    m_fHue = random( 255 );\n    m_fSpeedX = random( 1, 2 );\n    m_fSwing = random(360);\n    m_fTailLength = random( 6, 20 );\n    m_fEarDeg = random(140);\n  }\n\n  void draw()\n  {\n    pushMatrix();\n    translate( m_fPosX, m_fPosY );\n\n    // body\n    fill( m_fHue, 255, 255 );\n    rect( 0, 15, 35, 20 );\n\n    // head\n    fill( m_fHue, 255, 255 );\n    rect( 30, 0, 20, 20 );\n\n    // ears\n    pushMatrix();\n    translate( 32, 0 );\n    rotate( radians(m_fEarDeg)+QUARTER_PI*sin(radians(m_fSwing/2.0)) );\n    rect( -2, 0, 4, 8 );\n    popMatrix();\n\n    // legs\n    pushMatrix();\n    translate( 4, 35 );\n    rotate( QUARTER_PI*sin(radians(m_fSwing)) );\n    rect( -4, 0, 8, 20 );\n    popMatrix();\n\n    // arms\n    pushMatrix();\n    translate( 32, 35 );\n    rotate( -QUARTER_PI*sin(radians(m_fSwing)) );\n    rect( -4, 0, 8, 20 );\n    popMatrix();\n\n    // tail\n    pushMatrix();\n    translate( 0, 17 );\n    rotate( radians(225)-HALF_PI*sin(radians(m_fSwing/2.0)) );\n    rect( 0, -2, m_fTailLength, 4 );\n    popMatrix();\n\n    popMatrix();\n  }\n\n  void update()\n  {\n    m_fPosX += m_fSpeedX*abs(sin(radians(m_fSwing/2.0-90)));\n    if ( m_fPosX > width )\n    {\n      m_fPosX = 0;\n    }\n    m_fSwing += 6;\n    if ( m_fSwing > 360 )\n    {\n      m_fSwing = 0;\n    }\n  }\n}'}</CodeBlock>
