interface SidebarItem {
  label: string;
  key: number;
  nestedList?: SidebarItem[];
}

const Programming: FC = () => {
  const { param } = useParams();
  const [paramCur, setParamCur] = useState<string>("");
  const navigate = useNavigate(); // useNavigate
  const [sidebarElem,setSidebarElem] = useState<number[]>([-1,-1,-1]);


  // 
  const handleLinkClick = (selectIdx: number, selectedLayer: number) => {
    
    // URL
    let updatedSidebarElem = [...sidebarElem];

    if( selectedLayer === 0)
    { // Lesson
      updatedSidebarElem[selectedLayer] = selectIdx;
    }
    else
    { // Practice
      updatedSidebarElem[selectedLayer] = selectIdx + 1;  // 01
    }

    // 0
    for( let iLayer = selectedLayer + 1; iLayer < updatedSidebarElem.length; iLayer++ )
    {
      updatedSidebarElem[iLayer] = 0;
    }

    //setSidebarElem(updatedSidebarElem);

    navigate(`${NavigateRoot}${updatedSidebarElem[0]}-${updatedSidebarElem[1]}-${updatedSidebarElem[2]}`);
  };

  // 
  const handleCurLessonClick = () => {
    // URL
    let updatedSidebarElem = [...sidebarElem];
    updatedSidebarElem[1] = 0;  // Practice ID 0
    navigate(`${NavigateRoot}${updatedSidebarElem[0]}-${updatedSidebarElem[1]}-${updatedSidebarElem[2]}`);
  };

  useEffect(() => {

    if( param )
    {
      // URL
      const parts = param.split('-'); // 
      const numbers = parts.map(part => parseInt(part, 10)); // 
      setSidebarElem( numbers);
      setParamCur(param);
    }
    
  }, [param]);

  return (
    <div className="Programming">
      {/* ContentFrame */}
      {paramCur !== "" && <ContentFrame LessonPrefix={LessonPrefix} param={paramCur} />}
      {/* Navigator */}
      {sidebarElem[0] >= 0 && <Navigator onLinkClicked={handleLinkClick} onCurLessonClick={handleCurLessonClick} sidebarElem={sidebarElem} sidebarItems={sidebarData} />}
    </div>
  );
};

export default Programming;
