        <CodeBlock>{'int iX = 0;\nfloat fHSB[] = { 0, 0, 0 };\n\nvoid setup()\n{\n  size( 300, 200 );\n  // \n  fHSB[0] = random( 360 );\n  fHSB[1] = 100;\n  fHSB[2] = 100;\n}\n\nvoid draw()\n{\n  background( 204 );\n  \n  drawChiikawa( iX, 100, fHSB[0], fHSB[1], fHSB[2] );\n  \n  update();\n}\n\nvoid drawChiikawa( int iX, int iY, float fH, float fS, float fB )\n{\n  pushMatrix();\n  \n  translate( iX, iY );\n  \n  colorMode( HSB, 360, 100, 100 );\n  strokeWeight( 2 );\n  \n  fill( fH, fS, fB );\n  ellipse( 0, 0, 80, 70 );\n  arc( -23, -27, 14, 20, radians( -220 ), radians( -10 ) );\n  arc( 23, -27, 14, 20, radians( -170 ), radians( 40 ) );\n  \n  fill( 0 );\n  circle( -13, -2, 9 );\n  circle( 13, -2, 9 );\n  \n  line( -18, -12, -10, -13 );\n  line( 18, -12, 10, -13 );\n  \n  fill( fH, fS, fB );\n  noStroke();\n  ellipse( -13, -4, 6, 5 );\n  ellipse( 13, -4, 6, 5 );\n\n  stroke( 0 );\n  arc( -3, 10, 6, 6, radians( 0 ), radians( 180 ) );\n  arc( 3, 10, 6, 6, radians( 0 ), radians( 180 ) );\n  \n  popMatrix();\n}\n\nvoid update()\n{\n  iX += 2;\n  if( iX > 350 )\n  {\n    // \n    fHSB[0] = random( 360 );\n    fHSB[1] = 100;\n    fHSB[2] = 100;\n    iX = 0;\n  }\n}'}</CodeBlock>
