        <CodeBlock>{'int iFrame = 0;\nfloat fRGB[] = { 0, 0, 0 };\n\nvoid setup()\n{\n  size( 400, 400 );\n  // \n  fRGB[0] = random( 255 );\n  fRGB[1] = random( 255 );\n  fRGB[2] = random( 255 );\n}\n\nvoid draw()\n{\n  background( 204 );\n    \n  drawDejoukun( 200, 200, iFrame, fRGB[0], fRGB[1], fRGB[2] );\n  \n  update();\n}\n\nvoid drawDejoukun( int iX, int iY, float iDeg, float fR, float fG, float fB )\n{\n  pushMatrix();\n  \n  translate( iX, iY );\n  rotate( radians( iDeg ) );\n    \n  noStroke();  // fuchi nashi\n\n  // finger\n  fill( fR, fG, fB );\n  ellipse( 100, -49, 104, 136 );\n  \n  // finger mask\n  fill( 204 );\n  ellipse( 190, -70, 200, 200 );\n  \n  // daen\n  fill( fR, fG, fB );\n  ellipse( 0, 30, 260, 200 );\n  \n  // mouth mask\n  fill( 204 );\n  ellipse( 0, 4, 116, 186 );\n  \n  // ue daen mask\n  fill( fR, fG, fB );\n  ellipse( 0, -4, 200, 134 );\n  \n  // daen mask\n  fill( 204 );\n  ellipse( 0, 4, 90, 60 );\n  \n  popMatrix();\n}\n\nvoid update()\n{\n  // 30\n  if( iFrame % 30 == 0 )\n  {\n    fRGB[0] = random( 255 );\n    fRGB[1] = random( 255 );\n    fRGB[2] = random( 255 );\n  }\n\n  iFrame++;\n  if( iFrame >= 360 )\n  {\n    iFrame = 0;\n  }\n}'}</CodeBlock>
