        <CodeBlock>{'// \nfloat fDejouScale = 0;\nfloat fDejouY = 200;\n\n// \nint iSecond = 0;\nint iSecondFrame = 60;        // 0~60\nint iMinute = 0;\nint iMinuteFrame = 60 * 60;   // 0~3600\nint iHour = 0;\nint iHourFrame = 60 * 60 * 60;// 0~216000\n\nvoid setup()\n{\n  size( 400, 400 );\n  iSecond = second();\n  iMinute = minute();\n  iHour = hour();\n  frameRate( 60 );\n}\n\nvoid draw()\n{\n  background( 204 );\n\n  // \n  updateTime();\n\n  // \n  updateAnim();\n\n  // \n  drawPrimitives();\n}\n\n/**\n *  \n *  \n */\nvoid drawPrimitives()\n{\n  drawDejoukun( 200, fDejouY, fDejouScale );\n}\n\nvoid drawDejoukun( int iX, float fY, float fScaleX )\n{\n  pushMatrix();\n  \n  translate( iX, fY );\n  scale( fScaleX, 1 );\n  \n  noStroke();  // fuchi nashi\n\n  // finger\n  fill( 0 );\n  ellipse( 100, -49, 104, 136 );\n  \n  // finger mask\n  fill( 204 );\n  ellipse( 190, -70, 200, 200 );\n  \n  // daen\n  fill( 0 );\n  ellipse( 0, 30, 260, 200 );\n  \n  // mouth mask\n  fill( 204 );\n  ellipse( 0, 4, 116, 186 );\n  \n  // ue daen mask\n  fill( 0 );\n  ellipse( 0, -4, 200, 134 );\n  \n  // daen mask\n  fill( 204 );\n  ellipse( 0, 4, 90, 60 );\n  \n  popMatrix();\n}\n\n/**\n *  \n *  if\n */\nvoid updateAnim()\n{\n  if( iMinuteFrame < 180 )  // \n  {\n    fDejouY = 200 - 100 * sin( radians( iMinuteFrame ) );\n  }\n  \n  fDejouScale = sin( radians( 30 + iSecondFrame * 2 ) );\n}\n\n/**\n *  \n *  \n */\nvoid updateTime()\n{\n  // \n  if ( iSecond!= second() )  // \n  {\n    iSecond = second();\n    iSecondFrame = 0;\n  }\n  else\n  {\n    iSecondFrame++;\n  }\n\n  // \n  if ( iMinute!= minute() )  // \n  {\n    iMinute = minute();\n    iMinuteFrame = 0;\n  }\n  else\n  {\n    iMinuteFrame++;\n  }\n\n  // \n  if ( iHour!= hour() )     // \n  {\n    iHour = hour();\n    iHourFrame = 0;\n  }\n  else\n  {\n    iHourFrame++;\n  }\n}'}</CodeBlock>
