        <CodeBlock>{'// \nfloat fFaceX = 0;\nfloat fFaceY = 0;\nfloat fFaceHSB[] = { 0, 0, 0 };\nfloat fFaceMinScale = 1;\n\n// \nint iSecond = 0;\nint iSecondFrame = 60;        // 0~60\nint iMinute = 0;\nint iMinuteFrame = 60 * 60;   // 0~3600\nint iHour = 0;\nint iHourFrame = 60 * 60 * 60;// 0~216000\n\nvoid setup()\n{\n  size( 400, 400 );\n  iSecond = second();\n  iMinute = minute();\n  iHour = hour();\n  frameRate( 60 );\n  // \n  fFaceHSB[0] = random( 360 );\n  fFaceHSB[1] = 100;\n  fFaceHSB[2] = 100;\n}\n\nvoid draw()\n{\n  background( 204 );\n\n  // \n  updateTime();\n\n  // \n  updateAnim();\n\n  // \n  drawPrimitives();\n}\n\n/**\n *  \n *  \n */\nvoid drawPrimitives()\n{\n  drawChiikawa( fFaceX, fFaceY, 1, fFaceHSB[0], fFaceHSB[1], fFaceHSB[2] );\n  \n  drawChiikawa( 200, 350, fFaceMinScale, fFaceHSB[0], fFaceHSB[1], fFaceHSB[2] );\n}\n\nvoid drawChiikawa( float fX, float fY, float fScale, float fH, float fS, float fB )\n{\n  pushMatrix();\n  \n  translate( fX, fY );\n  scale( fScale );\n  \n  colorMode( HSB, 360, 100, 100 );\n  strokeWeight( 2 );\n  \n  fill( fH, fS, fB );\n  ellipse( 0, 0, 80, 70 );\n  arc( -23, -27, 14, 20, radians( -220 ), radians( -10 ) );\n  arc( 23, -27, 14, 20, radians( -170 ), radians( 40 ) );\n  \n  fill( 0 );\n  circle( -13, -2, 9 );\n  circle( 13, -2, 9 );\n  \n  line( -18, -12, -10, -13 );\n  line( 18, -12, 10, -13 );\n  \n  fill( fH, fS, fB );\n  noStroke();\n  ellipse( -13, -4, 6, 5 );\n  ellipse( 13, -4, 6, 5 );\n\n  stroke( 0 );\n  arc( -3, 10, 6, 6, radians( 0 ), radians( 180 ) );\n  arc( 3, 10, 6, 6, radians( 0 ), radians( 180 ) );\n  \n  popMatrix();\n}\n\n/**\n *  \n *  if\n */\nvoid updateAnim()\n{\n  // \n  if( iMinuteFrame < 240 )  // \n  {\n    fFaceMinScale = 3 - 2 * iMinuteFrame / 240.0;\n  }\n  else                      // \n  {\n    fFaceMinScale = 1 + 2 * iMinuteFrame / 3600.0;\n  }\n  \n  // \n  if( iSecondFrame == 0 )       // \n  {\n    // \n    fFaceX = 50 + iSecond * 5;\n    fFaceY = 0;\n    \n    // \n    fFaceHSB[0] = random( 360 );\n    fFaceHSB[1] = 100;\n    fFaceHSB[2] = 100;\n  }\n  else  // X\n  {\n    fFaceX += random( -4, 4 );\n    fFaceY += 6;\n  }\n}\n\n/**\n *  \n *  \n */\nvoid updateTime()\n{\n  // \n  if ( iSecond!= second() )  // \n  {\n    iSecond = second();\n    iSecondFrame = 0;\n  }\n  else\n  {\n    iSecondFrame++;\n  }\n\n  // \n  if ( iMinute!= minute() )  // \n  {\n    iMinute = minute();\n    iMinuteFrame = 0;\n  }\n  else\n  {\n    iMinuteFrame++;\n  }\n\n  // \n  if ( iHour!= hour() )     // \n  {\n    iHour = hour();\n    iHourFrame = 0;\n  }\n  else\n  {\n    iHourFrame++;\n  }\n}'}</CodeBlock>
